import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { post } from '../../api_client';

import './Login-styles.css';


class Login extends Component {
  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
      errors: ""
    };
    this.handleLoginForm = this.handleLoginForm.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.goToPage = this.goToPage.bind(this);
    this.disappear = this.disappear.bind(this);
  }

  disappear(){
    this.setState({errors: ""})
  }

  goToPage(path) {
    this.props.history.push(path)
  };

  handleChange(event){
    const target = event.target;
    const name = target.name;
    const value = target.value;
    this.setState({
      [name]: value
    });
  }

  setSession(data){
    sessionStorage.setItem('user_id', data.id)
    this.props.updateState('user_id', data.id)
    setTimeout(this.goToPage, 500, '/dashboard')
  }

  handleLoginForm(event) {
    event.preventDefault();
    const stateData = { ...this.state };
    const loginData = {user: stateData}
    const sessionsPath = `login`
    post(sessionsPath, loginData)
      .then(
        data => this.setSession(data)
      )
      .catch(
        error => this.setState({errors: error.errors})
      )

  }

  render() {
    return (
    <div className="login-page">
      <form
        className="form-container login-form"
        onSubmit={ this.handleLoginForm }
      >
      <div className="login-form">
      <label>
        <h4> Email Address </h4>
        <input
          className="signin-input"
          name="email"
          autoFocus
          type="text"
          required={true}
          inputMode="text"
          onChange={ this.handleChange }
          />
      </label>
      <label>
        <h3>Password </h3>
        <input
        className="signin-input"
        name="password"
        required={true}
        type="password"
        inputMode="text"
        onChange={this.handleChange}
        />
      </label>
      </div>
        <div className="login-error-box" onClick={this.disappear}>
          { this.state.errors }
        </div>
        <div className="button-containers login-button-container">
          <button
            className="login-button form-submit"
            type="submit"
            >Login</button>
        </div>
      </form>
    </div>
    )
  };
};

export default withRouter(Login);



