import React, {Component} from 'react';
import './HouseholdData-styles.css';

class HouseholdData extends Component{
  constructor(props){
    super(props);
    this.state = {
      id: this.props.data.id,
      no_residents: this.props.data.number_residents,
      total_sq_ft: this.props.data.total_sq_ft,
      avg_consumption: this.props.data.avg_total_electricty_consumption_per_resident,
      avg_savings: this.props.data.avg_total_electricty_savings_per_resident,
      no_bills: this.props.data.number_of_bills_entered,
      total_consumed: this.props.data.total_electricity_consumption_to_date,
      total_savings: this.props.data.total_electricity_savings_to_date,
      users_ids: this.props.data.users_id,
      address: this.props.data.address.address_line1,
      city: this.props.data.address.city,
      state: this.props.data.address.state,
      country: this.props.data.address.country,
      neighborhood: this.props.data.address.neighborhood_name,

    }
  }

  render(){
    return(
      <div className="household-container">
        <h1> Household Data </h1>
        <div className="household-data-container">
          
        </div>
      </div>
    )
  }
}

export default HouseholdData;



