import React, {Component} from 'react';
import HouseholdData from '../HouseholdData';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import IconButton from 'material-ui/IconButton';
import ActionHome from 'material-ui/svg-icons/action/home';
import {get} from '../../api_client';

import "typeface-roboto";
import './HouseholdPage-styles.css';

const styles = {
  largeIcon: {
    color: '#415B2F',
    width: 198,
    height: 198,
    alignContent: 'center',
    justifyContent: 'center',
    paddingBottom: '10px',
  },
  icon: {
    width: 196,
    height: 176,
    padding: 24,
    position: 'absolute',
    bottom: 30,
    right: 20,
    marginTop: -20,
  }
}

class HouseholdPage extends Component{
  constructor(props){
    super(props);
    this.state = {
      house_id: this.props.data.house_id,
      loaded: false,
    }
    this.setLoad = this.setLoad.bind(this);
    this.reloadPage = this.reloadPage.bind(this);
    this.getData = this.getData.bind(this);
  }

  componentDidMount(){
    window.addEventListener('beforeunload', this.reloadPage)
    if(this.state.house_id === null){
      let id = localStorage.getItem('house_id')
      this.resetHouseId(id)
    }else{
      this.getData()
    }
  }

  getData(){
    let id = this.state.house_id
    const path = `api/v1/houses/${id}`
    get(path)
      .then(data => this.setLoad(data))
      .catch(error => console.log(error))
  }

  setLoad(data){
    this.setState({
      data: data,
      loaded: true
    })
  }

  resetHouseId(id){
    this.setState({
      house_id: id
    }, () => {
      this.getData()
    });
  }

  componentWillUnmount(){
    window.removeEventListener('beforeunload', this.reloadPage)
  }

  reloadPage(){
    localStorage.setItem('house_id', this.state.house_id)
  }

  render(){
    let loaded = this.state.loaded
    return(
      <div>
      { loaded ? <HouseholdData data={this.state.data} /> : null }
      <MuiThemeProvider>
      <IconButton className="home-icon-space" iconStyle={styles.largeIcon} style={styles.icon}>
        <ActionHome className="home-icon"/>
      </IconButton>
      </MuiThemeProvider>
      </div>
    )
  }
}

export default HouseholdPage;



