import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import {get} from '../../api_client';
import './BillsPage-styles.css'

const Bill = function(props){
  console.log(props)
    return(
      <div className="bill-container">
        <h3> Start: {props.bill.start_date}</h3>
        <h3> End: {props.bill.end_date} </h3><br/>
        <h3> {props.type} used: {props.bill.total_used} </h3>
        <h3> price: ${props.bill.price} </h3>
        <br/>
        <h3> carbon footprint: {props.bill.carbon_impact}</h3>
      </div>
    )
}

function capitalize(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

class PastBillsPage extends Component{
  constructor(props){
    super(props)
    this.state = {
      billType: localStorage.getItem('resource_type'),
      loaded: false
    }
    this.loadBills = this.loadBills.bind(this);
  }

  componentDidMount(){
    this.returnBills()
  }

  returnBills(){
    const path = `api/v1/users/${this.props.user_id}/bills/${this.state.billType}`
    get(path)
      .then(data => this.setState({billArray: data, loaded: true}))
      .catch(error => console.log(error))
  }

  loadBills(){
    console.log(this.state.billArray.length)
    if(this.state.billArray.length === 0){
      return(
        <div className="no-bills-container">
          <h3>No Bills to Date</h3>
          <p>add new bill
            <a href="" onClick={() => this.props.history.push('/managebills')}>
              __here__
            </a>
          </p>
        </div>
      );
    }else{
      return this.state.billArray.map((bill, n) => {
        return(
          <Bill key={n} bill={bill} type={this.state.billType}/>
        );
      });
    }
  }

  render(){
    let loaded = this.state.loaded;

    return(
      <div className="bills-page">
        <div className="bills-page-header">
          <h1> Past {capitalize(this.state.billType)} Bills</h1>
        </div>
      <div className="bills-page-div">
        {loaded ? this.loadBills() : null }
      </div>
      </div>
    )
  }
}

export default withRouter(PastBillsPage);



