import React from 'react';
import ReactDOM from 'react-dom';
import './reset.css';
import { BrowserRouter } from 'react-router-dom';
import App from './components/App';

const CarbonCollective = (
  <BrowserRouter>
    <App />
  </BrowserRouter>
)

ReactDOM.render(
  CarbonCollective, document.getElementById('root')
);



