import React, {Component} from 'react';
import Select from 'react-select'
import {withRouter} from 'react-router-dom';
import {get} from '../../api_client';

import './Neighborhood-styles.css';

class NeighborhoodInput extends Component{
  constructor(props){
    super(props);
    this.state = {
      city: sessionStorage.getItem('city_id'),
      cityName: '',
      neighborhood: null,
      neighborhoods: null,
      loading: true,
      disabled: false,
      first: {class: 'text-show'},
      second: {class: 'hidden'},
      third: {class: 'hidden'},
      form: {class: 'hidden'},
      buttonFind: {class: "confirm-neighborhood-btn"},
      buttonForm: {class: "confirm-neighborhood-btn red"},
      clearbutton: {display: 'none'},
    }
    this.loadNeighborhoods = this.loadNeighborhoods.bind(this)
    this.loadCityName = this.loadCityName.bind(this);
    this.setNeighborhood = this.setNeighborhood.bind(this)
    this.setNeighborhoods = this.setNeighborhoods.bind(this)
    this.clearRegion = this.clearRegion.bind(this);
    this.neighborhoodPopup = this.neighborhoodPopup.bind(this);
    this.text2 = this.text2.bind(this);
    this.text3 = this.text3.bind(this);
    this.renderNeighborhoodForm = this.renderNeighborhoodForm.bind(this);
    this.backToRegionPick = this.backToRegionPick.bind(this);
    this.storeAndgoToAddressPage = this.storeAndgoToAddressPage.bind(this);
    this.goToAddressPage = this.goToAddressPage.bind(this);
    this.scrollTop = this.scrollTop.bind(this);
  }

  componentDidMount(){
    this.loadNeighborhoods(this.state.city)
    this.loadCityName(this.state.city)
  }

  componentDidUpdate(){
  }

  scrollTop(){
    var elemn = document.getElementById("neighborhoodform-input")
    console.log(elemn)
  }

  text2(){
    this.setState({
      first: {class: 'hidden'},
      second: {class: 'text-show'}
    })
  }

  text3(){
    this.setState({
      second: {class: 'hidden'},
      third: {class: 'text-show neighborhood-link-container'}
    })
  }

  clearRegion(){
    this.setState({neighborhood: null, disabled: false, clearbutton:{display: 'none'}})
  }

  loadNeighborhoods(id){
    get(`api/v1/areas/neighborhoods?city_id=${id}`)
        .then(data => this.setNeighborhoods(data))
        .catch(error => console.log(error))
  }

  loadCityName(id){
    get(`api/v1/areas/cities/show/${id}`)
      .then(data => this.setState({cityName: data.name}))
      .catch(error => console.log(error))
  }

  setNeighborhood(newValue){
    let id = newValue.value
    this.setState({neighborhood: id, disabled: true, clearbutton:{display: 'inline-block'}});
  }

  setNeighborhoods(data){
    const hoods = data.map((val) => {
      return {value: val.id, label: val.name, className: 'neighborhood-list-options'}
    })
    this.setState({
      neighborhoods: hoods,
      loading: false
    })
  }

  backToRegionPick(){
    this.props.history.push('/search_address')
  }

  renderNeighborhoodForm(event){
    if(event.target.className === 'confirm-neighborhood-btn red'){
      alert('We know you probably already know this. But have you checked your neighborhood name to be sure?')
      this.setState({
        buttonForm: {class: 'confirm-neighborhood-btn'},
      })
    }else{
      this.setState({
        welcomeStyle: {display: 'none'},
        third: {class: 'hidden'},
        form: {class: 'neighborhood-form-container'}
      })
    }

  }

  neighborhoodPopup(){
    window.open("https://www.denvergov.org/maps/map/neighborhoods");
    this.setState({
      buttonFind: {class: 'confirm-neighborhood-btn red'},
      buttonForm: {class: 'confirm-neighborhood-btn'}
    })
  }

  storeAndgoToAddressPage(){
    let nId = this.state.neighborhood
    if(nId !== null && nId !== ''){
      sessionStorage.setItem('neighborhood_id', nId)
      this.goToAddressPage()
    }else{
      return alert("You must choose a neighborhood to continue")
    }
  }

  goToAddressPage(){
    const path = '/add_address'
    this.props.history.push(path)
  }

  render(){
    if(this.state.loading){
      return(
        <div></div>
      )
    }else{
      return(
        <div className="region-expandable-list-box outside-div">
          <div className="neighborhood-header">
            <p>Neighborhood Select</p>
          </div>
          <div className="neighborhood-welcome" style={this.state.welcomeStyle}>
            <h3 className={this.state.first.class} onClick={this.text2}>We at CarbonCollective are passionate about thinking locally -- as this is where we can impact the most change!</h3>
            <h3 className={this.state.second.class} onClick={this.text3} > As such, we invite you to start thinking in terms of small localities -- aka your neighborhood! -- as this is where most advanced-level points can be won.</h3>
            <div className={this.state.third.class}>
              <h3>
                We see you're in {this.state.cityName}. Below you'll find a link to {this.state.cityName}'s officially recognized neighborhoods:
              </h3>
              <button
              className={this.state.buttonFind.class}
              onClick={this.neighborhoodPopup}
              >Confirm Your Neighborhood</button>
              <h3> Please click the link above, type in your address, and confirm the name of your neighborhood before continuing...</h3>
              <button
              className={this.state.buttonForm.class}
              onClick={this.renderNeighborhoodForm}
              >Continue</button>
            </div>
          </div>
          <div className={this.state.form.class}>
            <Select
              className="neighborhoodForm"
              name="neighborhood-form-field"
              value={this.state.neighborhood}
              placeholder={this.state.cityName + "'s Neighborhoods"}
              inputProps={{autoComplete: 'off', placeholder: 'click here to select neighborhood'}}
              options={this.state.neighborhoods}
              onChange={this.setNeighborhood}
              disabled={this.state.disabled}
              matchPos='any'
              id="neighborhoodform-input"
              />
              <div className='forward-backward-links-container-neighborhoods'>
                <button
                  className="region-next"
                  onClick={this.backToRegionPick}
                  style={this.state.backbutton}
                  >Start Over</button>
                <button
                  className="region-next clear-region"
                  onClick={this.clearRegion}
                  style={this.state.clearbutton}
                  >Clear {this.props.regionName}</button>
                <button
                  className="region-next"
                  onClick={this.storeAndgoToAddressPage}
                  >Next </button>
              </div>
            </div>
        </div>
      )
    }
  }
}

export default withRouter(NeighborhoodInput);



