import React, { Component } from 'react';
import './AddAddress-styles.css'
import { withRouter } from 'react-router-dom';
import { countries } from '../utilities';
import { get, post } from '../../api_client'

class AddAddress extends Component {

  constructor() {
    super();
    let city_id = sessionStorage.getItem('city_id')
    let neighborhood_id = sessionStorage.getItem('neighborhood_id')
    this.state = {
      message: "Please enter your address below",
      messageColor: {color: ' #29d967'},
      address_line1: "",
      address_line2: "",
      city_id: city_id,
      city: "",
      region: "",
      country: "",
      zip: "",
      neighborhood_id: neighborhood_id,
      loading: true,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleAddressForm = this.handleAddressForm.bind(this);
    this.goToPage = this.goToPage.bind(this);
    this.setSession = this.setSession.bind(this);
    this.setAddressComponents = this.setAddressComponents.bind(this);
  }

  componentDidMount(){
    get(`api/v1/areas/cities/show/${this.state.city_id}`)
      .then(data => this.setAddressComponents(data))
      .catch(error => console.log(error))
  }

  setAddressComponents(cityData){
    this.setState({
      city: cityData.name,
      region: cityData.region,
      country: cityData.country,
      loading: false,
    })
  }

  goToPage(path) {
    this.props.history.push(path)
  }

  resetForm() {
    this.setState({
      message: "Please enter your address below",
      messageColor: {color: ' #29d967'},
      address_line1: "",
      address_line2: "",
      city: "",
      region: "",
      country: "",
      zip: "",
      neighborhood_id: "",
      loading: false,
    })
  }

  generateCountryOptions() {
    return countries.map((country, i) => {
      return (
        <option key={ i } value={ country }>{ country }</option>
      )
    }
  )}

  handleChange(event) {
    const target = event.target;
    const name = target.name;
    const value = target.value;
    console.log(name)
    this.setState({
      [name]: value
    });
  }

  setSession(data){
    sessionStorage.setItem('address_id', data.id);
    this.resetForm();
    this.goToPage('/add_household');
  }

  handleAddressForm(event) {
    event.preventDefault();
    let addressData = {
      address_line1: this.state.address_line1,
      address_line2: this.state.address_line2,
      city_id: this.state.city_id,
      neighborhood_id: this.state.neighborhood_id,
      };
    addressData = {address: addressData, zipcode: this.state.zip};
    const apiPath = `addresses`
    let addressConf = `${this.state.address_line1}, ${this.state.city}, ${this.state.region} ${this.state.zip}`
    //make API call to post new address
    if(confirm(`We have your address as: ${addressConf}. Please confirm if this is accurate`)){
      post(apiPath, addressData)
        .then(data => this.setSession(data))
        .catch(error => this.setState({message: error.errors, messageColor: {color: "#ED3838"}}))
    }else{
      alert('Please fix your address and try again')
    }

  };

  render() {
    let loading = this.state.loading;
    if(loading){
      return(
        <div>
        </div>
      )
    }
    return (
    <div className='add-address-page'>
      <form
        className="form-container add-address"
        onSubmit={ this.handleAddressForm }
      >
        <h4 style={this.state.messageColor}>{this.state.message}</h4>
        <label>
          <h5>Address Line 1</h5>
          <input
            type="text"
            autoFocus
            required={ true }
            inputMode="text"
            name="address_line1"
            onChange={ this.handleChange }
          />
        </label>
        <label>
          <h5>Address Line 2</h5>
          <input
            type="text"
            required={ false }
            inputMode="text"
            name="address_line2"
            onChange={ this.handleChange }
          />
        </label>
        <label>
          <h5>Zipcode</h5>
          <input
            type="text"
            required={ true }
            inputMode="numeric"
            name="zip"
            onChange={ this.handleChange }
          />
        </label>
        <div className="button-containers address-button-container">
        <button
        className="form-submit address-submit"
        type="submit"
        >Save Address</button>
        </div>
      </form>
    </div>
    )
  };
};

export default withRouter(AddAddress);



