import React, { Component } from 'react';
import './ManageBills-styles.css'
import { withRouter } from 'react-router-dom';
import { get, post } from '../../api_client'


function assignMetric(res){
  if(res==='electricity'){
    return 'kwhs'
  }else if(res === 'water'){
    return 'gallons'
  }else if(res === 'gas'){
    return 'therms'
  }
}

function capitalize(name){
  return name.charAt(0).toUpperCase() + name.slice(1);
}

class ManageBills extends Component {
  constructor() {
    super();
    let resource = localStorage.getItem('resource_type') || 'electricity'
    let type = assignMetric(resource)
    resource = capitalize(resource)
    this.state = {
      type: type,
      resource: resource,
      errors: "",
      start_date: "",
      end_date: "",
      price: 0,
      house_id: null,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleAddBillsForm = this.handleAddBillsForm.bind(this);
    this.goToPage = this.goToPage.bind(this);
    this.billSuccess = this.billSuccess.bind(this);
    this.assignBillPath = this.assignBillPath.bind(this);
  }

  componentDidMount(){
    let user_id = sessionStorage.getItem("user_id")
    let user_path = `api/v1/users/${user_id}`
    get(user_path)
      .then(data => this.setState({house_id: data.household.id}))
      .catch(error => console.log(error))
  }

  assignBillPath(){
    console.log(this.state.resource)
    if(this.state.resource === 'Electricity'){return 'electric_bills'}
    else if(this.state.resource === 'Water'){ return 'water_bills'}
    else if(this.state.resource === 'Gas'){ return 'gas_bills'};
  }

  billSuccess(data){
    console.log(data)
    this.resetForm();
    this.goToPage('/dashboard');
  }

  goToPage(path) {
    this.props.history.push(path)
  }

  resetForm() {
    this.setState({
      errors: "",
      start_date: "",
      end_date: "",
      total_start: 0,
      price: 0,
      house_id: null,
    })
  }

  handleChange(event) {
    const target = event.target;
    const name = target.name;
    const value = target.value;
    this.setState({
      [name]: value,
    });
  }

  handleAddBillsForm(event) {
    event.preventDefault();
    let householdData = this.state;
    const path = this.assignBillPath()
    console.log(path)
    //make API call to post new address
    householdData = {[path]: householdData}
      post(path, householdData)
        .then(data => this.billSuccess(data))
        .catch(error => console.log(error))
    // API call above

  };

  render() {

    let { start_date, end_date, bill_cost } = this.state;
    let isFormValid = true
    // (start_date !== "" && end_date !== "" && total_kwatts !== 0 && bill_cost !== 0);
    let formResource = "total_" + this.state.type
    return (
      <form
        className="form-container manage-bills"
        onSubmit={ this.handleAddBillsForm }
      >
        <h3 className="bills-title">Add your latest {localStorage.getItem('resource_type') } bill below</h3>
        <label>
          <h5>Start Date</h5>
          <input
            required="true"
            type="date"
            name="start_date"
            value={ start_date }
            onChange={ this.handleChange }
          />
        </label>
        <label>
          <h5>End Date</h5>
          <input
            required="true"
            type="date"
            name="end_date"
            value={ end_date }
            onChange={ this.handleChange }
          />
        </label>
        <label>
          <h5>Total {capitalize(this.state.type)}</h5>
          <input
            required="true"
            type="number"
            name={formResource}
            placeholder="Bills Reported Use"
            min="0"
            value={ this.state.formResource }
            onChange={ this.handleChange }
          />
        </label>
        <label className="bill-cost">
          <h5>Bill Cost</h5>
          <input
            // required="true"
            type="number"
            name="price"
            placeholder="Total Bill Cost"
            min="0"
            value={ bill_cost }
            onChange={ this.handleChange }
          />
        </label>
        <div className="button-containers bills-button-container">
          <button
            className="form-submit"
            disabled={ !isFormValid }
            type="submit"
          >Save {this.state.resource} Bill</button>
        </div>
      </form>
    )
  };
};

export default withRouter(ManageBills);



