const host = 'https://carboncollective.herokuapp.com'
// const host = 'http://localhost:3000'
// const headers = {
//   'content-type': 'application/json'
// }

function handleResponse(response) {
  if (response.ok) {
    return response.json();
  }
  return response.text().then(text => {
    const error = JSON.parse(text)
    return Promise.reject({ ...error, status: response.status })
  })
}

export function get(path) {
  return fetch(`${host}/${path}`, {
    // method: 'get',
    // mode: 'no-cors',
  }).then(handleResponse)
}

export function post(path, data) {
  return fetch(`${host}/${path}`, {
    method: 'post',
    body: JSON.stringify(data),
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    }
  }).then(handleResponse)
}

export function put(path, id, data) {
  if(id === undefined){
    return fetch(`${host}/${path}`, {
      method: 'put',
      body: JSON.stringify(data),
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': 'https://carboncollective.herokuapp.com',
      }
    }).then(handleResponse)
  }else{
    return fetch(`${host}/${path}/${id}`, {
      method: 'put',
      body: JSON.stringify(data),
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': 'https://carboncollective.herokuapp.com',
      }
    }).then(handleResponse)
  }
}

export function destroy(path) {
  return fetch(`${host}/${path}`, {
    method: 'delete',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      'Access-Control-Allow-Origin': 'https://carboncollective.herokuapp.com',
    }
  }).then(handleResponse)
}



