import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {withRouter} from 'react-router-dom';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import IconButton from 'material-ui/IconButton';
import ActionHome from 'material-ui/svg-icons/action/account-box';
import SettingsIcon from 'material-ui/svg-icons/action/settings';
import BackArrow from 'material-ui/svg-icons/av/replay';

import "typeface-roboto";
import './Navbar-styles.css'

const styles = {
  mediumIcon: {
    color: '#415B2F',
    width: 48,
    height: 48,
    alignContent: 'center',
    justifyContent: 'center',
    paddingBottom: '10px',
  },
  left: {
    width: 96,
    height: 76,
    padding: 24,
    position: 'relative',
    top: 5,
    marginTop: -20,
  },
  middle: {
    width: 96,
    height: 76,
    padding: 24,
    position: 'relative',
    top: 5,
    marginTop: -20,
  },
  right: {
    width: 96,
    height: 76,
    padding: 24,
    position: 'relative',
    top: 5,
    marginTop: -20,
    verticalAlign: 'center'
  },
};

function addColor() {
  this.style.backgroundColor = '#29d967'
}

function removeColor() {
  this.style.backgroundColor = undefined
}

class Navbar extends Component {
    constructor(){
      super()
      this.goToDash = this.goToDash.bind(this)
      this.goToSettings = this.goToSettings.bind(this)
    }

    static contextTypes = {
      router: PropTypes.object
    }

    render() {
    if (['/', '/search_address', '/add_neighborhood'].indexOf(window.location.pathname) > -1) {
      return(
        <div>
        </div>
      )
    }else if(['/login', '/signup', '/add_address', '/add_household'].indexOf(window.location.pathname) > -1){
      return(
        <MuiThemeProvider>
          <footer>
            <div className="footer-navbar">
              <ul className="navbar">
                <li className="footer-button">
                <IconButton onMouseEnter={addColor} onMouseLeave={removeColor} onClick={this.context.router.history.goBack} iconStyle={styles.mediumIcon} style={styles.left}>
                  <BackArrow className="svg-icon" />
                </IconButton>
                </li>
              </ul>
            </div>
          </footer>
        </MuiThemeProvider>
      )
    }else if(window.location.pathname === '/dashboard'){
      return (
      <MuiThemeProvider>
        <footer>
          <div className="footer-navbar">
            <ul className="navbar">
              <li className="footer-button">
              <IconButton onMouseEnter={addColor} onMouseLeave={removeColor} onClick={this.context.router.history.goBack} iconStyle={styles.mediumIcon} style={styles.left}>
                <BackArrow  className="svg-icon"/>
              </IconButton>
              </li>
              <li className="footer-button">
              <IconButton onMouseEnter={addColor} onMouseLeave={removeColor} onClick={this.goToSettings} iconStyle={styles.mediumIcon} style={styles.right}>
                <SettingsIcon className="svg-icon"/>
              </IconButton>
              </li>
            </ul>
          </div>
        </footer>
      </MuiThemeProvider>
      )
    }else if(window.location.pathname === '/settings'){
      return (
      <MuiThemeProvider>
        <footer>
          <div className="footer-navbar">
            <ul className="navbar">
              <li className="footer-button">
              <IconButton onMouseEnter={addColor} onMouseLeave={removeColor} onClick={this.context.router.history.goBack} iconStyle={styles.mediumIcon} style={styles.left}>
                <BackArrow  className="svg-icon"/>
              </IconButton>
              </li>
              <li className="footer-button">
              <IconButton onMouseEnter={addColor} onMouseLeave={removeColor} onClick={this.goToDash} iconStyle={styles.mediumIcon} style={styles.right}>
                <ActionHome className="svg-icon"/>
              </IconButton>
              </li>
            </ul>
          </div>
        </footer>
      </MuiThemeProvider>
      )
    }else{
      return (
      <MuiThemeProvider>
        <footer>
          <div className="footer-navbar">
            <ul className="navbar">
              <li className="footer-button">
              <IconButton onMouseEnter={addColor} onMouseLeave={removeColor} onClick={this.context.router.history.goBack} iconStyle={styles.mediumIcon} style={styles.left}>
                <BackArrow  className="svg-icon"/>
              </IconButton>
              </li>
              <li className="footer-button">
              <IconButton onMouseEnter={addColor} onMouseLeave={removeColor} onClick={this.goToDash} iconStyle={styles.mediumIcon} style={styles.middle}>
                <ActionHome className="svg-icon"/>
              </IconButton>
              </li>
              <li className="footer-button">
              <IconButton onMouseEnter={addColor} onMouseLeave={removeColor} onClick={this.goToSettings} iconStyle={styles.mediumIcon} style={styles.right}>
                <SettingsIcon className="svg-icon"/>
              </IconButton>
              </li>
            </ul>
          </div>
        </footer>
      </MuiThemeProvider>
      )
    }
  }
  goToDash(){
    this.props.history.push('/dashboard')
  }
  goToSettings(){
    this.props.history.push('/settings')
  }
}

export default withRouter(Navbar);



